@import "mixin";

#hotPizza {
  @include flexCenterColomn;
  background-image: url("../img/backgrounds/bg_4.jpg");
  text-align: center;
  height: 1000px;

  > :nth-child(1) {
    color: white;
    margin-top: 4%;
    height: 20%;
  }

  > :nth-child(2) {
    color: #444343;
    margin-top: 4%;
    width: 50%;
    height: 20%;
  }

  > :nth-child(3) {
    @include flexCenterRow;
    flex-wrap: wrap;
    width: 100%;
    height: 80%;
    margin-top: 4%;


    > :first-child {
      @include flexCenterRow;
      height: 50%;
      padding: 0;

      > div {
        display: flex;
        height: 100%;
        width: 33.33%;

        > :first-child {
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          height: 100%;
          width: 50%;
          padding: 0;

          > img {
            height: 100%;
            width: 100%;
            margin: 0;
          }

        }


        > div {
          @include flexCenterColomn;
          align-items: flex-start;
          height: auto;
          padding: 4%;
          width: 42%;

          > span {
            margin: 0 0 4% 0;
            color: white;
          }
          > p {
            margin: 0 0 4% 0;
            color: #444343;
            text-align: left;
          }

          > div {
            @include flexCenterRow;
            justify-content: flex-start;
            margin: 0;
            width: 100%;

            > span {
              color: #fac565;
              margin: 0;
            }
          }
        }
      }

      > :nth-child(3) {
        > div {
          >img {
            width: 150% !important;
          }
        }
      }
    }

    > :last-child {
      @include flexCenterRow;
      height: 50%;
      width: 100%;
      padding: 0;
      > div {
        display: flex;
        width: 33.33%;
        height: 100%;

        > :first-child {
          @include flexCenterColomn;
          align-items: flex-start;
          height: auto;
          padding: 4%;
          width: 42%;

          > span {
            margin: 0 0 4% 0;
            color: white;
          }
          > p {
            margin: 0 0 4% 0;
            color: #444343;
            text-align: left;
          }

          > div {
            @include flexCenterRow;
            justify-content: flex-start;
            margin: 0;
            width: 100%;
            height: min-content;

            > span {
              color: #fac565;
              margin: 0;
            }
          }
        }
        > :last-child {
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          height: 100%;
          width: 50%;
          padding: 0;
          > img {
            height: 100%;
            width: 100%;
            margin: 0;
          }
        }
      }

      > :nth-child(3) {
        > div {
          >img {
            width: 150% !important;
          }
        }
      }
    }
  }
}