@mixin html {
  margin: 0 auto;
}

@mixin toCenterFlex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flexCenterColomn {
  @include toCenterFlex;
  flex-direction: column;
}

@mixin flexCenterRow {
  @include toCenterFlex;
  flex-direction: row;
}

@mixin clearDecor($color) {
  text-decoration: none;
  color: $color;
}

@mixin clearAll($color) {
  text-decoration: none;
  color: $color;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background: none;
}




