#blog {
  display: flex;
  flex-direction: column;
  background-image: url("../img/backgrounds/bg_4.jpg");
  text-align: center;
  justify-content: center;
  > :first-child {
    color: white;
    height: 10%;
  }

  > :nth-child(2) {
    color: #444343;
    height: 10%;
  }

  > :last-child {
    display: flex;
    flex-direction: row;
    height: 80%;
    width: 92%;

    > div {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 30%;
      height: 100%;
      justify-content: space-between;
      img {
        width: 100%;
      }
      :nth-child(2) {
        color: #444343;
        margin: 0;
      }
      :nth-child(3) {
        color: white;
        margin: 0;
      }
      :nth-child(4) {
        color: #444343;
        overflow: hidden;
        margin: 0;
      }
    }
  }
}