footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #101315;
  > section {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 70%;
    justify-content: space-around;
    align-items: flex-start;
    padding-top: 4%;

    > :nth-child(1) {
      display: flex;
      flex-direction: column;
      text-align: left;
      width: 20%;
      height: 100%;
      padding-left: 4%;
      > :nth-child(1) {
        color: white;
        margin: 0;
      }
      > :nth-child(2) {
        color: #555555;
        margin: 0;
      }
      > :nth-child(3) {
        display: flex;
        flex-direction: row;
        width: 80%;
        margin: 0;
        > img {
          width: 10%;
          border-radius: 100%;
          background-color: #f1f1f1;
          margin: 0;
        }
      }
    }

    > :nth-child(2) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 40%;
      height: 100%;
      > p {
        height: 10%;
        color: white;
      }
      > div {
        height: 36%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        > img {
          width: 30%;
          height: 100%;
        }
        > div {
          display: flex;
          flex-direction: column;
          > :first-child {
            color: white;
            margin: 0;
          }
          > :last-child {
            color: #555555;
            margin: 0;
          }
        }
      }

    }

    > :nth-child(3) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 20%;
      height: 40%;
      > p {
        color: white;
        width: 100%;
        padding-bottom: 5%;
        margin-left: 10%;
      }
    }

    > :nth-child(4) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 20%;
      height: 80%;
      text-align: left;
      > p {
        color: white;
        margin: 0;
      }
      > div {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;
        > img {
          background-color: white;
          border-radius: 100%;
          margin: 0 4% 4% 0;
        }
        > p {
          overflow: hidden;
          color: #888888;
          margin: 0;
        }
      }
    }
  }
  > :last-child {
    background-color: #121618;
    display: flex;
    flex-direction: row;
    margin: 0;
    color: white;
    justify-content: center;
    height: 30%;
    align-items: center;
      > a {
        color: red;
        text-decoration: none;
        margin: 0;
      }
  }
}