@import "mixin";

#restaurantInfo {
  @include flexCenterColomn;
  width: 100%;
  margin: 0;

  address {
    margin: 0;
    width: 100%;
    height: 150px;
    @include flexCenterRow;

    section {
      @include flexCenterRow;
      width: 70%;
      height: 100%;
      background-color: black;
      div {
        @include flexCenterRow;
        height: 100%;
        align-items: flex-start;
        justify-content: flex-start;
        margin-top: 8%;

        img {
          border-radius: 100%;
          background-color: #fac565;
          margin-right: 3%;
        }

        div {
          @include flexCenterColomn;
          justify-content: flex-start;

          :nth-child(1) {
            margin-bottom: 6%;
            color: white;
          }
          :nth-child(2) {
            color: #444343;
          }
        }
      }
    }
  }
}
#social {
  @include flexCenterRow;
  width: 30%;
  background-color: #fac565;
  height: 100%;
  margin: 0;
  justify-content: flex-start;
  padding-left: 3%;

  img {
    margin: auto 3%;
  }
 }
#welcome {
  @include flexCenterRow;
  background-image: url("../img/backgrounds/bg_4.jpg");

  div {
    width: 50%;
    img {
      width: 100%;

    }
  }
  section {
    @include flexCenterColomn;
    width: 50%;
    margin: 3%;
  }
}