@import "mixin";
@import "animations";

header {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  nav {
    width: 100%;
    background-color: #121618;
    height: 100px;
    @include flexCenterRow;
    justify-content: space-around;
  }
}

.links {
  @include flexCenterRow;

  a {
    @include clearDecor(white);
    @include toCenterFlex;
    margin: 0 10px;
    border: none;
  }
}

.logo {
  color: orange;
}

.imageInform {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300%;
}

.headerContent {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 0;
  position: relative;
  left: 0%;
  animation-name: headerRemoves;
  animation-duration: 30s;
  animation-iteration-count: infinite;
}

#info1 {
  @include flexCenterRow;
  background-image: url("../img/backgrounds/bg_1.jpg");
  width: 100%;

  > :nth-child(1) {
    width: 30%;
    @include flexCenterColomn;
    align-items: flex-end;
    margin: 0;

    p {
      margin: 0;
      color: white;
    }

    > :nth-child(1) {
      color: #fac565;
    }

    div {
      @include flexCenterRow;
      justify-content: space-between;
      width: 100%;
      margin: 0;
    }
  }

  img {
    width: 30%;
    margin: 0 0 0 2%;
  }
}

#info2 {
  @include flexCenterRow;
  background-image: url("../img/backgrounds/bg_1.jpg");
  width: 100%;

  > :nth-child(2) {
    width: 30%;
    @include flexCenterColomn;
    align-items: flex-start;
    margin: 0;

    p {
      margin: 0;
      color: white;
    }

    > :nth-child(1) {
      color: #fac565;
    }

    div {
      @include flexCenterRow;
      justify-content: space-between;
      width: 100%;
      margin: 0;
    }
  }

  img {
    width: 30%;
    margin: 0 2% 0 0;
  }
}

#info3 {
  width: 100%;
  @include flexCenterRow;
  background-image: url("../img/backgrounds/bg_3.jpg");
  background-position-y: -60px;
  background-size: 100% 139%;
  background-repeat: no-repeat;

  :nth-child(1) {
    width: 60%;
    @include flexCenterColomn;
    align-items: center;
    margin: 0;

    p {
      margin: 0;
      color: white;
    }

    > :nth-child(1) {
      color: #fac565;
    }

    div {
      @include flexCenterRow;
      justify-content: space-between;
      width: 100%;
      margin: 0;
    }
  }
}