@mixin btnHeader() {
  text-decoration: none;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 10%;
  padding: 4%;
  width: 50%;
  height: 100%;
}

.btnT1 {
  @include btnHeader;
  background: none;
  border: 1px solid white;
  color: white;
}

.btnT1:hover {
  transition: 0.2s;
  background-color: #fac565;
  color: black;
  border: none;
}

.btnT2 {
  @include btnHeader;
  border: 1px solid #fac565;
  background-color: #fac565;
}

.btnT2:hover {
  transition: 0.2s;
  background-color: transparent;
  border: 1px solid white;
  color: white;
}

.btnT3 {
  @include btnHeader;
  color: #888888;
  border: none;
  background-color: transparent;
}

.btnT3:hover {
  transition: 0.2s;
  background-color: transparent;
  border: 1px solid white;
  color: white;
}





