@import "mixin";

#ourServices {
  background-image: url("../img/backgrounds/food.png");
  @include flexCenterColomn;
  text-align: center;
  padding: 6%;

  > :nth-child(1) {
    margin-bottom: 4%;
  }

  > :nth-child(2) {
    margin-bottom: 4%;

  }

  > :last-child {
    @include flexCenterRow;

    > div {
      @include flexCenterColomn;
      justify-content: flex-start;
      > div {
        border: 1px solid orange;
        margin-bottom: 4%;
        width: min-content;
        padding: 8%;
      }
      > span {
        margin-bottom: 4%;
      }
      > p {
        margin-bottom: 10%;
      }
    }

  }

}